import zolysoftNavBar from "@/colorui/components/zolysoft-nav-bar/zolysoft-nav-bar.vue"
import uniCard from "@/colorui/components/uni-card/uni-card"
export default {
	data() {
		return {
			username: '13778424516',
			password: '123456',
			baseUrl: 'http://47.104.29.236:9999/api/v1/'
		}
	},
	components: {
		zolysoftNavBar,
		uniCard
	},
	methods: {
		// 登录
		Login() {
			// 判断登录不能为空
			if (!this.username) {
				uni.showToast({
					title: "账号不能为空",
					image: '../../static/toast/error.png'
				});
				return;
			}
			// 判断账号格式
			let userReg =  /^[1][3,4,5,7,8][0-9]{9}$/
			if (!userReg.test(this.username)) {
				uni.showToast({
					title: "电话号码格式不正确",
					image: '../../static/toast/error.png'
				});
				return;
			}
			// 密码不能位空
			if (!this.password) {
				uni.showToast({
					title: "密码不能为空",
					image: '../../static/toast/error.png'
				});
				return;
			}
			// 判断密码长度
			if (this.password.length < 6 || this.password.length > 12) {
				uni.showToast({
					title: "密码长度为6 - 12 位",
					image: '../../static/toast/error.png'
				});
				return;
			}
			uni.showLoading({
				title: 'loading'
			});
			uni.request({
				url: this.baseUrl + '/login',
				method: 'POST',
				header: {
					"content-type": "application/x-www-form-urlencoded"
				},
				data: {
					username: this.username,
					password: this.password
				},
				success: function(res) {
					console.log(res.data);
					if (res.data.code == '200') {
						uni.setStorage({
							key: 'token',
							data: res.data.token
						})
						uni.setStorage({
							key: 'user_id',
							data: res.data.id
						})
						// const value = uni.getStorageSync('user_id');
						// const value1 = uni.getStorageSync('token');
						// console.log(value, value1)
						uni.hideLoading();
						uni.reLaunch({
							url: '../index/index'
						})
					} else {
						uni.showToast({
							title: res.data.msg,
							image: "../../static/toast/error.png"
						});
					}
				},
				fail: function(error) {
					console.log(error)
				}
			})
		},
		// 计算高度
		getClientHight() {
			let that = this;
			uni.getSystemInfo({
				success(res) {
					// console.log(res.screenHeight); //获取手机设备屏幕高度
					that.clientHight = res.screenHeight;
					that.footerHight = that.clientHight * 0.1;
					// console.log(that.footerHight)
				}
			})
		}
	}
}
